<template>
	<Dashboard>
		<div class="mb-4">
			<div class="mb-4 text-white">
				<h5>Kelola Atribut</h5>
				<small>Pengaturan / <b>Kelola Atribut</b></small>
			</div>

			<div class="card mb-4">
				<div class="card-body">
					
					<ul class="nav nav-tabs mb-4" id="myTab" role="tablist" style="border-bottom: none;">
						<!-- <li class="nav-item nav-tab-link">
							<a class="nav-link active" @click="showTable('document-type')" id="document-type-tab" data-toggle="tab" href="#document-type" role="tab" aria-controls="document-type" aria-selected="true">Tipe Dokumen</a>
						</li> -->
						<li class="nav-item nav-tab-link">
							<a class="nav-link active" @click="showTable('code-name')" id="code-name-tab" data-toggle="tab" href="#code-name" role="tab" aria-controls="code-name" aria-selected="false">Nama Kode</a>
						</li>
						<li class="nav-item nav-tab-link">
							<a class="nav-link" @click="showTable('permit')" id="permit-tab" data-toggle="tab" href="#permit" role="tab" aria-controls="permit" aria-selected="false">Nama Izin</a>
						</li>
						<li class="nav-item nav-tab-link">
							<a class="nav-link" @click="showTable('instance')" id="instance-tab" data-toggle="tab" href="#instance" role="tab" aria-controls="instance" aria-selected="false">Instansi</a>
						</li>
						<li class="nav-item nav-tab-link">
							<a class="nav-link" @click="showTable('sub-instance')" id="sub-instance-tab" data-toggle="tab" href="#sub-instance" role="tab" aria-controls="sub-instance" aria-selected="false">Sub Instansi</a>
						</li>
						<li class="nav-item nav-tab-link">
							<a class="nav-link" @click="showTable('pic')" id="pic-tab" data-toggle="tab" href="#pic" role="tab" aria-controls="pic" aria-selected="false">Penanggung Jawab</a>
						</li>
						<li class="nav-item nav-tab-link">
							<a class="nav-link" @click="showTable('department')" id="department-tab" data-toggle="tab" href="#department" role="tab" aria-controls="department" aria-selected="false">Departemen</a>
						</li>
						<li class="nav-item nav-tab-link">
							<a class="nav-link" @click="showTable('hardcopy-location')" id="hardcopy-location-tab" data-toggle="tab" href="#hardcopy-location" role="tab" aria-controls="hardcopy-location" aria-selected="false">Lokasi Hardcopy</a>
						</li>
						<li class="nav-item nav-tab-link">
							<a class="nav-link" @click="showTable('report-name')" id="report-name-tab" data-toggle="tab" href="#report-name" role="tab" aria-controls="report-name" aria-selected="false">Nama Pelaporan</a>
						</li>
						<!-- <li class="nav-item nav-tab-link">
							<a class="nav-link" @click="showTable('category')" id="category-tab" data-toggle="tab" href="#category" role="tab" aria-controls="category" aria-selected="false">Kategori</a>
						</li> -->
					</ul>

					<department v-if="selectedTable == 'department'"></department>
					<instance v-if="selectedTable == 'instance'"></instance>
					<hardcopy-location v-if="selectedTable == 'hardcopy-location'"></hardcopy-location>
					<category v-if="selectedTable == 'category'"></category>
					<code-name v-if="selectedTable == 'code-name'"></code-name>
					<sub-instance v-if="selectedTable == 'sub-instance'"></sub-instance>
					<pic v-if="selectedTable == 'pic'"></pic>
					<permit v-if="selectedTable == 'permit'"></permit>
					<report-name v-if="selectedTable == 'report-name'"></report-name>
				</div>
			</div>
		</div>

	</Dashboard>
</template>

<script type="text/javascript">

	import 'vue-ads-table-tree/dist/vue-ads-table-tree.css'
	import 'vue-ads-table-tree/dist/vue-ads-table-tree.umd.js'

	import DocumentType from '@/components/pages/setting/AttrManagement/DocumentType.vue'
	import Department from '@/components/pages/setting/AttrManagement/Department.vue'
	import Instance from '@/components/pages/setting/AttrManagement/Instance.vue'
	import HardcopyLocation from '@/components/pages/setting/AttrManagement/HardcopyLocation.vue'
	import Category from '@/components/pages/setting/AttrManagement/Category.vue'
	import CodeName from '@/components/pages/setting/AttrManagement/CodeName.vue'
	import SubInstance from '@/components/pages/setting/AttrManagement/SubInstance.vue'
	import PIC from '@/components/pages/setting/AttrManagement/PIC.vue'
	import Permit from '@/components/pages/setting/AttrManagement/Permit.vue'
	import ReportName from '@/components/pages/setting/AttrManagement/ReportName.vue'

	export default{
		name : 'AttributeManagement',
		components: {
			Department,
			Instance,
			HardcopyLocation,
			CodeName,
			SubInstance,
			'pic' : PIC,
			Permit,
			ReportName,
		},
		data() {
			return {
				selectedTable: 'code-name',
			}
		},
		mounted(){
			this.parseFromLink();
		},
		methods: {
			parseFromLink(){
				let hash = this.$router.currentRoute?.hash;
				if(hash){
					$(hash + "-tab").click();
					this.selectedTable = hash.replace("#", "");
				}
			},
			showTable(table){
				this.$router.replace("attribute-management#" + table);
				this.selectedTable = table;
			}
		}
	}
</script>

<style scoped>
table tbody td{
	white-space: nowrap;
}
table thead th{
	white-space: nowrap;
}
</style>