<template>
	<div>
		
		<div class="table-wrapper" id="table-document-type">
			<div class="text-right">
				<button @click="showAdd" class="btn btn-success"><i class="fas fa-plus"></i> Tambah</button>
			</div>
			<hr>
			<vue-ads-table-tree
			:columns="columns"
			:rows="rows"
			:items-per-page="count"
			>
			<template slot="toggle-children-icon" slot-scope="props">
				<span v-show="props.expanded">
					<i class="fas fa-minus"></i>
				</span>
				<span v-show="!props.expanded">
					<i class="fas fa-plus"></i>
				</span>
			</template>

			<template slot="action" slot-scope="props">
				<button @click="showEdit(props.row)" class="mr-1 btn btn-sm btn-warning"><i class="fas fa-edit"></i></button>
				<button @click="showDelete(props.row.id)" class="btn btn-sm btn-danger"><i class="fas fa-trash"></i></button>
			</template>
		</vue-ads-table-tree>
	</div>


	<div class="modal fade" id="modalAdd" tabindex="-1" role="dialog" aria-labelledby="modalStatus" aria-hidden="true">
		<div class="modal-dialog" role="document">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="exampleModalLongTitle">Tipe Dokumen</h5>
					<button type="button" class="close" data-dismiss="modal" aria-label="Close">
						<span aria-hidden="true">&times;</span>
					</button>
				</div>
				<div class="modal-body">

					<div class="form-group">
						<label>Kode</label>
						<input type="text" v-model="model.code" class="form-control">
					</div>
					<div class="form-group">
						<label>Tipe Dokumen</label>
						<input type="text" v-model="model.name" class="form-control">
					</div>

				</div>

				<div class="modal-footer">
					<button type="button" class="btn btn-secondary" data-dismiss="modal">Batal</button>
					<button type="button" @click="onAdd" class="btn btn-primary">Simpan</button>
				</div>
			</div>
		</div>
	</div>
</div>
</template>

<script type="text/javascript">
	import VueAdsTableTree from 'vue-ads-table-tree';
	import 'vue-ads-table-tree/dist/vue-ads-table-tree.css'
	import 'vue-ads-table-tree/dist/vue-ads-table-tree.umd.js'

	import alertMixin from '@/mixins/alert'
	import { documentTypeAll, documentTypeAdd, documentTypeUpdate, documentTypeDelete } from '@/api/documentType'

	export default{
		name : 'DocumentType',
		mixins: [
		alertMixin,
		],
		components: {
			VueAdsTableTree,
		},
		data() {
			return {
				model: {
					id: null,
					code: null,
					name: null,
					documentTypeId: null,
					tableActionAuthorizationId: null,
				},
				columns: [
				{
					property: 'code',
					title: 'Kode',
					direction: null,
					filterable: true,
					collapseIcon: true,
				},
				{
					property: 'name',
					title: 'Tipe Dokumen',
					direction: null,
					filterable: true,
				},
				{
					property: 'action',
					title: 'Tindakan',
					direction: null,
					filterable: false,
					collapseIcon: false,
				},
				],
				rows: [
				],
				count: 10,
			}
		},
		mounted(){
			this.model.tableActionAuthorizationId = 1;
			this.parseDocumentType();
		},
		methods: {
			parseDocumentType(){
				this.rows = [];
				this.showLoading();

				let request = documentTypeAll();
				request
				.then((response) => {
					this.hideLoading();
					let data = response.data;

					this.model.id = null;
					this.model.code = null;
					this.model.name = null;

					data.forEach((v,i) => {
						this.rows.push({
							id: v.id,
							code: v.code,
							name: v.name,
						})
					})
					this.count = this.rows.length;
				})
				.catch((error) => {
					this.hideLoading();
					console.log(error);
					if(error.response?.status == 401){
						this.invalidSession();
					}
				})
			},
			showAdd(){
				$("#modalAdd").modal('show');
				this.model.code = null;
				this.model.name = null;
			},
			onAdd(){
				this.showLoading();

				if(this.model.id == null){
					let request = documentTypeAdd(this.model);
					request
					.then((response) => {
						this.hideLoading();
						
						this.$toast.open({
							message: 'Tipe Dokumen Berhasil Disimpan !',
							type: 'info',
							position: 'top-right',
						});
						$("#modalAdd").modal("hide");

						this.parseDocumentType();
					})
					.catch((error) => {
						this.hideLoading();
						this.showNotif("Terjadi Kesalahan");
						console.log(error);
						if(error.response?.status == 401){
							this.invalidSession();
						}
					})
				}else{
					let request = documentTypeUpdate(this.model.id,this.model);
					request
					.then((response) => {
						this.hideLoading();
						
						this.$toast.open({
							message: 'Tipe Dokumen Berhasil Disimpan !',
							type: 'info',
							position: 'top-right',
						});
						$("#modalAdd").modal("hide");

						this.parseDocumentType();
					})
					.catch((error) => {
						this.hideLoading();
						this.showNotif("Terjadi Kesalahan");
						console.log(error);
						if(error.response?.status == 401){
							this.invalidSession();
						}
					})
				}
			},
			showEdit(data){
				this.model.code = data.code;
				this.model.name = data.name;
				this.model.id = data.id;
				$("#modalAdd").modal("show");
			},
			showDelete(id){
				this.$swal({
					title: 'Hapus Data',
					text: "Yakin ingin menghapus data ?",
					type: 'error',
					showCancelButton: true,
					confirmButtonColor: '#3085d6',
					cancelButtonColor: '#d33',
					confirmButtonText: 'Yes'
				}).then((result) => {
					if (result.value) {
						this.showLoading();
						
						let request = documentTypeDelete(id);
						request
						.then((response) => {
							this.hideLoading();

							this.$toast.open({
								message: 'Tipe Dokumen Berhasil Dihapus !',
								type: 'info',
								position: 'top-right',
							});

							this.parseDocumentType();
						})
						.catch((error) => {
							this.hideLoading();
							this.showNotif("Terjadi Kesalahan");
							console.log(error);
							if(error.response?.status == 401){
								this.invalidSession();
							}
						})
					}
				});
			},
			async invalidSession(){
				alert("Sesi sudah tidak valid, silahkan login");
				await this.$store.dispatch("auth/authLogout");
				this.$router.replace("/login");
			}
		}
	}
</script>

<style type="text/css" scoped="">
	.vue-ads-w-full .svg-inline--fa.fa-w-14{
		width: 1.5em !important;
	}
</style>